* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


.app {
  height: 100vh;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  overflow-y: auto;
  scrollbar-width: none;
  color: white;
  background: url("../public/images/bg.jpeg");
}

.app::-webkit-scrollbar {
  display: none;
}